
import { Options, Vue } from "vue-class-component";

@Options({
  components: {},
})
export default class AddGoalComponent extends Vue {
  input = "";
  invalidInput = false;

  addGoal() {
    this.invalidInput = false;
    if (this.input === "") {
      this.invalidInput = true;
      return;
    } else {
      this.$emit("add-goal", this.input);
      this.input = "";
    }
  }
}
